body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.blurred-city {
    width: 100%;
    height: 100vh;
}

.weather-container {
    border-radius: 15px;
    width: 90vw;
    position: absolute;
    top: 100px;
    right: 20px;
}

.input_custom{
    width: 62vw;
    height: 7vw;
    z-index: 1000;
}

.ui.input>input{
    color: white;
}

.search_custom{
    font-size: 2vh;
}

.location{
    display: grid;

    grid-template-columns: 1fr auto 1fr;
    grid-gap: 10px;
    width: 90%;
    height: 78vh;
    text-align: center;

}

.temp_display{
    grid-column: 2 / 3;
    font-size: 30px;
    font-size: 4.5vw;
    color: white;
    align-self: center;
    justify-self: center;
}

.temp_info{
}

.header_display{
    font-size: 3vh;
    padding: 30px;
}

.header_display>p{
    font-size: 2vh;
    padding: 20px;
}
